import { CartForm, Image, Money } from '@shopify/hydrogen';
import { Link } from '@remix-run/react';
import { useVariantUrl } from '~/utils';
import "../styles/cart.css?__remix_sideEffect__";
/**
 * @param {CartMainProps}
 */
export function CartMain({ layout, cart }) {
  const linesCount = Boolean(cart?.lines?.nodes?.length || 0);
  const withDiscount =
  cart &&
  Boolean(cart.discountCodes.filter((code) => code.applicable).length);
  const className = `cart-main ${withDiscount ? 'with-discount' : ''}`;

  return (
    <div className={className}>
      <CartEmpty hidden={linesCount} layout={layout} />
      <CartDetails cart={cart} layout={layout} />
    </div>);

}

/**
 * @param {CartMainProps}
 */
function CartDetails({ layout, cart }) {
  const cartHasItems = !!cart && cart.totalQuantity > 0;

  return (
    <div className="cart-details">
      <CartLines lines={cart?.lines} layout={layout} />
      {cartHasItems &&
      <div className='cart-sumary-content'>
          <h2 className='title-cart'>Resumo</h2>
          <CartSummary cost={cart.cost} layout={layout}>
          <CartDiscounts discountCodes={cart.discountCodes} />
          <CartCheckoutActions checkoutUrl={cart.checkoutUrl} />
        </CartSummary>
        </div>}

    </div>);

}

/**
 * @param {{
 *   layout: CartMainProps['layout'];
 *   lines: CartApiQueryFragment['lines'] | undefined;
 * }}
 */
function CartLines({ lines, layout }) {
  if (!lines) return null;

  return (
    <div aria-labelledby="cart-lines">
      <ul>
        {lines.nodes.map((line) =>
        <CartLineItem key={line.id} line={line} layout={layout} />
        )}
      </ul>
    </div>);

}

/**
 * @param {{
 *   layout: CartMainProps['layout'];
 *   line: CartLine;
 * }}
 */
function CartLineItem({ layout, line }) {
  const { id, merchandise } = line;
  const { product, title, image, selectedOptions } = merchandise;
  const lineItemUrl = useVariantUrl(product.handle, selectedOptions);

  return (
    <li key={id} className="cart-line-item">
      {image &&
      <Image
        alt={title}
        aspectRatio="1/1"
        data={image}
        height={100}
        loading="lazy"
        width={100}
        className='img-cart' />}



      <div>
        <Link
          prefetch="intent"
          to={lineItemUrl}
          onClick={() => {
            if (layout === 'aside') {
              // close the drawer
              window.location.href = lineItemUrl;
            }
          }}
          className='cart-item-detail'>

          <p className='title-cart'>
           {product.title}
          </p>
         
        </Link>
        <p className='title-item-cart'>
           {product.description}
          </p>
        <CartLinePrice line={line} as="span" />
        <ul>
          {selectedOptions.map((option) =>
          <li key={option.name}>
              <small>
                {option.name}: {option.value}
              </small>
            </li>
          )}
        </ul>
        <CartLineQuantity line={line} />
      </div>
    </li>);

}

/**
 * @param {{checkoutUrl: string}}
 */
function CartCheckoutActions({ checkoutUrl }) {
  if (!checkoutUrl) return null;

  return (
    <div>
      <a href={checkoutUrl} target="_self">
        <p className='btn-checkout-cart'>Finalizar a compra &rarr;</p>
      </a>
      <br />
    </div>);

}

/**
 * @param {{
 *   children?: React.ReactNode;
 *   cost: CartApiQueryFragment['cost'];
 *   layout: CartMainProps['layout'];
 * }}
 */
export function CartSummary({ cost, layout, children = null }) {
  const className =
  layout === 'page' ? 'cart-summary-page' : 'cart-summary-aside';

  return (
    <div aria-labelledby="cart-summary-content" className={className}>
     
      <dl className="cart-total">
        
      <h4 className='titutlo'>Sub total:</h4>
        <dd>
          {cost?.subtotalAmount?.amount ?
          <p className='price-cart'>
              <Money data={cost?.subtotalAmount} />
            </p> :

          '-'}

        </dd>
      </dl>
      <dl className="cart-total">
        
      <h4 className='titutlo'>Total</h4>
        <dd>
          {cost?.subtotalAmount?.amount ?
          <p className='price-cart'>
              <Money data={cost?.subtotalAmount} />
            </p> :

          '-'}

        </dd>
      </dl>
      {children}
    </div>);

}

/**
 * @param {{lineIds: string[]}}
 */
function CartLineRemoveButton({ lineIds }) {
  return (
    <CartForm
      route="/cart"
      action={CartForm.ACTIONS.LinesRemove}
      inputs={{ lineIds }}>

      <button className='remove-btn' type="submit">Remove</button>
    </CartForm>);

}

/**
 * @param {{line: CartLine}}
 */
function CartLineQuantity({ line }) {
  if (!line || typeof line?.quantity === 'undefined') return null;
  const { id: lineId, quantity } = line;
  const prevQuantity = Number(Math.max(0, quantity - 1).toFixed(0));
  const nextQuantity = Number((quantity + 1).toFixed(0));

  return (
    <div className="cart-line-quantiy">
      <small className='qyt'>Quantity: {quantity} &nbsp;&nbsp;</small>
      <CartLineUpdateButton lines={[{ id: lineId, quantity: prevQuantity }]}>
        <button
          aria-label="Decrease quantity"
          disabled={quantity <= 1}
          name="decrease-quantity"
          value={prevQuantity}
          className='decrease-btn'>

          <span>&#8722; </span>
        </button>
      </CartLineUpdateButton>
      &nbsp;
      <CartLineUpdateButton lines={[{ id: lineId, quantity: nextQuantity }]}>
        <button
          aria-label="Increase quantity"
          name="increase-quantity"
          value={nextQuantity}
          className='increase-btn'>

          <span>&#43;</span>
        </button>
      </CartLineUpdateButton>
      &nbsp;
      <CartLineRemoveButton lineIds={[lineId]} />
    </div>);

}

/**
 * @param {{
 *   line: CartLine;
 *   priceType?: 'regular' | 'compareAt';
 *   [key: string]: any;
 * }}
 */
function CartLinePrice({ line, priceType = 'regular', ...passthroughProps }) {
  if (!line?.cost?.amountPerQuantity || !line?.cost?.totalAmount) return null;

  const moneyV2 =
  priceType === 'regular' ?
  line.cost.totalAmount :
  line.cost.compareAtAmountPerQuantity;

  if (moneyV2 == null) {
    return null;
  }

  return (
    <div>
      <p className='price'>
      <Money withoutTrailingZeros {...passthroughProps} data={moneyV2} />
      </p>
    </div>);

}

/**
 * @param {{
 *   hidden: boolean;
 *   layout?: CartMainProps['layout'];
 * }}
 */
export function CartEmpty({ hidden = false, layout = 'aside' }) {
  return (
    <div hidden={hidden} className='cart-sem'>
      <br />
      <p className='desc'>
      Parece que ainda não adicionou nada, vamos começar
      começar!
      </p>
      <br />
      <Link
        to="/collections/sneakers"
        onClick={() => {
          if (layout === 'aside') {
            window.location.href = '/collections';
          }
        }}
        className='shipping-btn'>

        Continue shopping →
      </Link>
    </div>);

}

/**
 * @param {{
 *   discountCodes: CartApiQueryFragment['discountCodes'];
 * }}
 */
function CartDiscounts({ discountCodes }) {
  const codes =
  discountCodes?.
  filter((discount) => discount.applicable)?.
  map(({ code }) => code) || [];

  return (
    <div>
      {/* Have existing discount, display it with a remove option */}
      <dl hidden={!codes.length}>
        <div>
          <dt>Discount(s)</dt>
          <UpdateDiscountForm>
            <div className="cart-discount">
              <code>{codes?.join(', ')}</code>
              &nbsp;
              <button className='remove-discount-btn'>Remove</button>
            </div>
          </UpdateDiscountForm>
        </div>
      </dl>

      {/* Show an input to apply a discount */}
      <UpdateDiscountForm discountCodes={codes}>
        <div className='discount-content'>
          <input type="text" name="discountCode" placeholder="Codigo Promocional" />
          &nbsp;
          <button className='apply-btn' type="submit">Apply</button>
        </div>
      </UpdateDiscountForm>
    </div>);

}

/**
 * @param {{
 *   discountCodes?: string[];
 *   children: React.ReactNode;
 * }}
 */
function UpdateDiscountForm({ discountCodes, children }) {
  return (
    <CartForm
      route="/cart"
      action={CartForm.ACTIONS.DiscountCodesUpdate}
      inputs={{
        discountCodes: discountCodes || []
      }}>

      {children}
    </CartForm>);

}

/**
 * @param {{
 *   children: React.ReactNode;
 *   lines: CartLineUpdateInput[];
 * }}
 */
function CartLineUpdateButton({ children, lines }) {
  return (
    <CartForm
      route="/cart"
      action={CartForm.ACTIONS.LinesUpdate}
      inputs={{ lines }}>

      {children}
    </CartForm>);

}

/** @typedef {CartApiQueryFragment['lines']['nodes'][0]} CartLine */
/**
 * @typedef {{
 *   cart: CartApiQueryFragment | null;
 *   layout: 'page' | 'aside';
 * }} CartMainProps
 */

/** @typedef {import('@shopify/hydrogen/storefront-api-types').CartLineUpdateInput} CartLineUpdateInput */
/** @typedef {import('storefrontapi.generated').CartApiQueryFragment} CartApiQueryFragment */